@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Oswald", sans-serif;
}

.App {
  --margenes: 10vw;
  padding-left: var(--margenes);
  padding-right: var(--margenes);
}

.header {
  width: 100%;
  margin: 0 auto;
  background-color: #ddb892;
  height: 200px;
  text-align: left;
  padding-left: 50px;
  padding-top: 40px;
}

.filtros {
  width: 100%;
  background-color: #ffe5d9;
  height: 80px;
}

.hotel {
  width: 30%;
  border: 1px solid;
  height: auto;
  text-align: center;
  margin-bottom: 40px;
  margin-left: 15px;
  margin-right: 15px;
  padding-bottom: 15px;
}

.hoteles {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 50px;
}

.filtros-lista {
  padding-top: 30px;
  display: flex;
  justify-content: space-between;
}

.fecha {
  display: flex;
  flex-direction: column;
  color: white;
}
.fecha-desde {
  padding-left: 100px;
}
.btn-reset {
  padding-right: 100px;
}
.btnResetF {
  background-color: #f94144;
  width: 100px;
  border-radius: 8%;
  font-weight: bolder;
}
.btn-reserva {
  background-color: #ffe5d9;
  width: 90%;
  border-radius: 8%;
  font-size: 15px;
  font-weight: bolder;
  height: 35px;
  margin-top: 8px;
}
.mensajeError {
  font-size: 40px;
  padding-left: 17%;
  padding-top: 15%;
}

